
"use client";

import { useState, useEffect, useMemo } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { prescriptionManager, Prescription, detailedPatients, Patient, Suggestion } from "@/lib/constants";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger, DropdownMenuSeparator } from "@/components/ui/dropdown-menu";
import { Button } from "@/components/ui/button";
import { MoreHorizontal, Search, Lightbulb, Info, CheckCircle, XCircle, NotebookText } from "lucide-react";
import { Input } from "@/components/ui/input";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from "@/components/ui/dialog";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { SuggestionDialog } from "@/components/suggestion-dialog";
import { useToast } from "@/hooks/use-toast";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";

export default function Page() {
    const [prescriptions, setPrescriptions] = useState<Prescription[]>([]);
    const [searchQuery, setSearchQuery] = useState("");
    const [selectedPatient, setSelectedPatient] = useState<Patient | null>(null);
    const [isDetailDialogOpen, setDetailDialogOpen] = useState(false);
    const [suggestingFor, setSuggestingFor] = useState<Prescription | null>(null);
    const { toast } = useToast();


    useEffect(() => {
        const handleUpdate = (updatedPrescriptions: Prescription[]) => {
            setPrescriptions([...updatedPrescriptions]);
        };
        handleUpdate(prescriptionManager.getPrescriptions());
        const unsubscribe = prescriptionManager.subscribe(handleUpdate);
        return () => unsubscribe();
    }, []);

    const handleStatusChange = (id: string, status: Prescription['status']) => {
        prescriptionManager.updatePrescriptionStatus(id, status);
    };

    const handleViewPatientDetails = (patientName: string) => {
        const patient = detailedPatients.find(p => p.name === patientName);
        if (patient) {
            setSelectedPatient(patient);
            setDetailDialogOpen(true);
        }
    };
    
    const filteredPrescriptions = useMemo(() => {
        if (!searchQuery) {
            return prescriptions;
        }
        return prescriptions.filter(p => 
            p.patientName.toLowerCase().includes(searchQuery.toLowerCase())
        );
    }, [prescriptions, searchQuery]);
    
    const handleSaveSuggestion = (suggestionData: Omit<Suggestion, 'status'>) => {
        if (!suggestingFor) return;
        
        const patient = detailedPatients.find(p => p.name === suggestingFor.patientName);
        if (!patient) return;
        
        prescriptionManager.addSuggestion(suggestingFor.id, suggestionData, patient.id);
        toast({
            title: "Suggestion Submitted",
            description: `Your suggestion for ${suggestingFor.medicine} has been sent to ${suggestingFor.doctor} for review.`,
        });
        setSuggestingFor(null);
    };
    
    const getStatusBadge = (p: Prescription) => {
        if (p.suggestion?.status === 'pending') {
            return <Badge className="bg-yellow-500">Suggestion Pending</Badge>;
        }
        if (p.suggestion?.status === 'accepted') {
            return <Badge className="bg-green-500 flex items-center gap-1">
                <TooltipProvider>
                    <Tooltip>
                        <TooltipTrigger asChild>
                           <CheckCircle className="h-3 w-3" />
                        </TooltipTrigger>
                        <TooltipContent><p>Suggestion was accepted by the doctor.</p></TooltipContent>
                    </Tooltip>
                </TooltipProvider>
                Filled
            </Badge>;
        }
        if (p.suggestion?.status === 'rejected') {
             return <Badge variant="destructive" className="flex items-center gap-1">
                 <TooltipProvider>
                    <Tooltip>
                        <TooltipTrigger asChild>
                           <XCircle className="h-3 w-3" />
                        </TooltipTrigger>
                        <TooltipContent>
                            <p className="font-bold">Suggestion Rejected</p>
                            <p className="max-w-xs">{p.suggestion.rejectionReason}</p>
                        </TooltipContent>
                    </Tooltip>
                </TooltipProvider>
                 {p.status}
             </Badge>;
        }

        switch (p.status) {
            case 'Filled':
                return <Badge className="bg-green-500">Filled</Badge>;
            case 'Unavailable':
                return <Badge variant="destructive">Unavailable</Badge>;
            case 'Pending':
            default:
                return <Badge variant="secondary">Pending</Badge>;
        }
    }


    return (
        <TooltipProvider>
            <Card>
                <CardHeader>
                    <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                        <div>
                            <CardTitle>Manage Prescriptions</CardTitle>
                            <CardDescription>View and update the status of patient prescriptions.</CardDescription>
                        </div>
                        <div className="relative w-full md:w-64">
                            <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                            <Input 
                                placeholder="Search by patient name..."
                                className="pl-8"
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                            />
                        </div>
                    </div>
                </CardHeader>
                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Patient</TableHead>
                                <TableHead>Medicine</TableHead>
                                <TableHead>Dosage</TableHead>
                                <TableHead>Prescribing Doctor</TableHead>
                                <TableHead>Status</TableHead>
                                <TableHead><span className="sr-only">Actions</span></TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {filteredPrescriptions.map((p) => (
                                <TableRow key={p.id} className="cursor-pointer" onClick={() => handleViewPatientDetails(p.patientName)}>
                                    <TableCell className="font-medium">{p.patientName}</TableCell>
                                    <TableCell>{p.medicine}</TableCell>
                                    <TableCell>{p.dosage}</TableCell>
                                    <TableCell>{p.doctor}</TableCell>
                                    <TableCell>
                                       {getStatusBadge(p)}
                                    </TableCell>
                                    <TableCell className="text-right">
                                        <DropdownMenu>
                                            <DropdownMenuTrigger asChild>
                                                <Button variant="ghost" className="h-8 w-8 p-0" onClick={(e) => e.stopPropagation()}>
                                                    <span className="sr-only">Open menu</span>
                                                    <MoreHorizontal className="h-4 w-4" />
                                                </Button>
                                            </DropdownMenuTrigger>
                                            <DropdownMenuContent align="end" onClick={(e) => e.stopPropagation()}>
                                                <DropdownMenuItem onClick={() => handleStatusChange(p.id, 'Filled')} disabled={p.suggestion?.status === 'pending'}>Mark as Filled</DropdownMenuItem>
                                                <DropdownMenuItem onClick={() => handleStatusChange(p.id, 'Pending')}>Mark as Pending</DropdownMenuItem>
                                                <DropdownMenuItem className="text-destructive" onClick={() => handleStatusChange(p.id, 'Unavailable')} disabled={p.suggestion?.status === 'pending'}>Mark as Unavailable</DropdownMenuItem>
                                                <DropdownMenuSeparator />
                                                <DropdownMenuItem onClick={() => setSuggestingFor(p)} disabled={!!p.suggestion}>
                                                    <Lightbulb className="mr-2 h-4 w-4" />
                                                    Suggest Alternative
                                                </DropdownMenuItem>
                                            </DropdownMenuContent>
                                        </DropdownMenu>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                     {filteredPrescriptions.length === 0 && (
                        <div className="text-center py-10 text-muted-foreground">
                            No prescriptions found for "{searchQuery}".
                        </div>
                    )}
                </CardContent>
            </Card>

            {selectedPatient && (
                <Dialog open={isDetailDialogOpen} onOpenChange={setDetailDialogOpen}>
                    <DialogContent className="max-w-3xl">
                        <DialogHeader>
                            <div className="flex items-center gap-4">
                                <Avatar className="w-16 h-16 border">
                                    <AvatarImage src={`https://placehold.co/64x64.png?text=${selectedPatient.name.charAt(0)}`} alt={selectedPatient.name} data-ai-hint="person" />
                                    <AvatarFallback>{selectedPatient.name.charAt(0)}</AvatarFallback>
                                </Avatar>
                                <div>
                                    <DialogTitle className="text-2xl">{selectedPatient.name}</DialogTitle>
                                    <DialogDescription>
                                        Condition: <Badge variant={selectedPatient.condition === 'Critical' ? 'destructive' : 'secondary'}>{selectedPatient.condition}</Badge>
                                    </DialogDescription>
                                </div>
                            </div>
                        </DialogHeader>
                        
                        <div className="grid gap-6 mt-4 max-h-[60vh] overflow-y-auto pr-4">
                            <section>
                                <h3 className="font-semibold text-lg mb-2 flex items-center gap-2"><NotebookText className="w-5 h-5"/>Clinical Summary</h3>
                                <div className="p-3 border rounded-md bg-muted/50 text-sm text-muted-foreground">
                                    {selectedPatient.clinicalSummary || "No summary available."}
                                </div>
                            </section>

                            <section>
                                <h3 className="font-semibold text-lg mb-2">Prescriptions</h3>
                                <Table>
                                    <TableHeader>
                                        <TableRow>
                                            <TableHead>Medicine</TableHead>
                                            <TableHead>Dosage</TableHead>
                                            <TableHead>Status</TableHead>
                                        </TableRow>
                                    </TableHeader>
                                    <TableBody>
                                        {selectedPatient.prescriptions.map((p) => (
                                            <TableRow key={p.id}>
                                                <TableCell>{p.medicine}</TableCell>
                                                <TableCell>{p.dosage}</TableCell>
                                                <TableCell>
                                                    <Badge variant={p.status === 'Pending' ? 'secondary' : p.status === 'Unavailable' ? 'destructive' : 'default'} className={p.status === 'Filled' ? 'bg-green-500' : ''}>{p.status}</Badge>
                                                </TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                </Table>
                            </section>
                        </div>
                        
                        <DialogFooter>
                            <Button variant="outline" onClick={() => setDetailDialogOpen(false)}>Close</Button>
                        </DialogFooter>
                    </DialogContent>
                </Dialog>
            )}

            {suggestingFor && (
                <SuggestionDialog
                    isOpen={!!suggestingFor}
                    onClose={() => setSuggestingFor(null)}
                    prescription={suggestingFor}
                    onSuggestionSubmit={handleSaveSuggestion}
                />
            )}
        </TooltipProvider>
    );
}
